/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap');

body {
  font-family: 'Open Sans', sans-serif;
  background-color: #f9f9f9;
  margin: 0;
  padding: 0;
}

.contact-page {
  position: relative;
  text-align: center;
  padding: 50px 20px;
}

.background-image {
  background: url('../asset/background.jpg') no-repeat center center;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 60%;
  z-index: -1;
}

.contact-page h1 {
  font-size: 40px;
  color: #504d4d;
  margin-bottom: 40px;
}

.contact-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.contact-card {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 30px;
  margin: 10px;
  max-width: 300px;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 1;
}

.contact-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.contact-card h2 {
  font-size: 24px;
  margin: 20px 0 10px;
  color: #333;
}

.contact-card p {
  font-size: 16px;
  color: #777;
  margin-bottom: 20px;
  width: 80%;
}

.cta-button {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
  margin-top: auto;
  width: 50%;
  text-align: center;
}

.cta-button:hover {
  background-color: #0056b3;
}

.contact-icon {
  font-size: 50px;
  margin-bottom: 20px;
  color: #007bff;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .contact-cards {
    flex-direction: column;
    align-items: center;
  }

  .contact-card {
    max-width: 90%;
  }

  .contact-page h1 {
    font-size: 28px;
  }

  .contact-card h2 {
    font-size: 20px;
  }

  .contact-card p {
    font-size: 14px;
  }

  .cta-button {
    padding: 8px 16px;
  }
}
